  display: flex;
}

.frankly-form.loading .screen.fade, .frankly-form.done .screen.fade {
  opacity: 0;
  transition: opacity .5s ease 0s;
}

.frankly-form.loading .box, .frankly-form.done .box {
  position: absolute;
  top: 50%;
  display: block;
  width: 75px;
  height: 75px;
}

.frankly-form.loading .box .content, .frankly-form.done .box .content {
  position: relative;
  margin: auto;
}

